.bar-chart-container{
    margin-top: 10px;
    margin-left: 10px;
    /* width:1200px; */
    width: 100%;
    height:990px;
    margin-bottom:20px;
}

.bar-chart-element{
    height:calc(100% - 20px);
    width: calc(100% - 25px);
    overflow: auto;
}

::-webkit-scrollbar {
    height: 10px;
    cursor: pointer;
  }

::-webkit-scrollbar-track {
    background: #ffffff; 
  }

::-webkit-scrollbar-thumb {
    background: #2e2e2e; 
    border-radius: 10px;
  }

::-webkit-scrollbar-thumb:hover {
    background: #6c6c6c; 
    
  }